import React from 'react';
import Container from 'react-bootstrap/Container';
import styled, { ThemeProvider } from 'styled-components';
// TODO figure out a way to use the useDarkMode package with styled-components.
import useDarkMode from 'use-dark-mode';
import { ColorTheme, DARK_THEME, LIGHT_THEME } from '../utils/constants';
import Introduction from './home/Introduction';
import RecentProjects from './home/RecentProjects';
import Expertise from './home/Expertise';
import Links from './home/Links';
// import Bookshelf from './home/Bookshelf';
import Footer from './home/Footer';

const HomeContainer = styled(Container)`
  padding-top: 50px;
  color: ${(props) => props.theme.primaryColor};
  background-color: ${(props) => props.theme.bgColor};
`;

function theme(darkMode: boolean) {
  return () => {
    return darkMode ? DARK_THEME : LIGHT_THEME;
  };
}

function Home() {
  const { value: darkMode } = useDarkMode();
  // const theme = darkMode ? DARK_THEME : LIGHT_THEME;

  return (
    <ThemeProvider theme={theme(darkMode)}>
      <HomeContainer fluid>
        <Introduction />
        <RecentProjects />
        <Expertise />
        <Links />
        <Footer />
        {/* <Bookshelf /> */}
      </HomeContainer>
    </ThemeProvider>
  );
}

export default Home;
